.alert {
  padding: 30px;

  align-content: space-between;
  background-color: #f44336;
  color: white;
  opacity: 1;
  transition: opacity 0.6s;
  flex-direction: row;
  border-radius: 10px;
}

.alert.success {
  background-color: #00a32a;
}
.alert.info {
  background-color: #2196f3;
}
.alert.warning {
  background-color: #ff9800;
}